@mixin onTablet {
  @media (min-width: $tablet-min-width) {
    @content;
  }
}

@mixin onDesktop {
  @media (min-width: $desktop-min-width) {
    @content;
  }
}

@mixin contentPaddingInline {
  padding-inline: 20px;

  @include onTablet {
    padding-inline: 72px;
  }

  @include onDesktop {
    max-width: 1020px;
    margin-inline: auto;
    padding-inline: 120px;
  }
}

@mixin hover($property, $toValue) {
  transition: #{$property} 0.3s;
  &:hover {
    #{$property}: $toValue;
  }
}

@mixin pageGrid {
  --columns: 2;
  display: grid;
  column-gap: 20px;
  grid-template-columns: repeat(var(--columns), 1fr);

  @include onTablet {
    --columns: 6;
    column-gap: 24px;
  }

  @include onDesktop {
    --columns: 12;
  }
}

@mixin pageButtonStyle {
  display: flex;
  width: 100%;
  height: 56px;
  padding: 0 48px;

  font-weight: 700;
  line-height: 27px;

  cursor: pointer;
  justify-content: center;
  align-items: center;

  font-family: $main-front-family;
  color: #fff;
  background-color: $main-page-color;
  border-radius: 8px;
  border: 2px solid #f0f1f4;
  transition: color $effectDuration, background-color $effectDuration;

  &:hover {
    color: $main-page-color;
    background-color: #f8f8fa;
  }

  &:focus {
    color: #fff;
    background-color: #111;
  }
}
